import { applyMiddleware, createStore } from 'redux';
import createLogger from 'redux-logger';
import createSagaMiddleware from 'redux-saga';
import getRootReducer from 'reducer/root';

export default function createApplication(
    initialState = {},
    reducers = {},
    sagas = null
) {
    // Compose redux middleware
    const middleware = [];

    if (sagas) {
        middleware.push(createSagaMiddleware(sagas));
    }
    middleware.push(createLogger());
    const createStoreWithMiddleware = applyMiddleware(...middleware);

    const rootReducer = getRootReducer(reducers);

    const store = createStoreWithMiddleware(
        createStore
    )(rootReducer, initialState);

    if (module.hot) {
        // Enable Webpack hot module replacement for reducers
        module.hot.accept('../reducer/root', () => {
            const nextGetRootReducer = require('../reducer/root').default;
            const nextRootReducer = nextGetRootReducer(reducers);

            store.replaceReducer(nextRootReducer);
        });
    }

    return store;
}



